@media only screen and (min-width: 320px){
  body {
    margin-top: 97px;
  }
  .languages--fixed-header {
    top: 0;
    z-index: 999;
    position: fixed;
    background-color: #f0f3f5;
  }
  .header {
    top: 25px;
    z-index: 9999;
    position: fixed;
  }
}

@media only screen and (min-width: 1024px){
  .header, .languages {
    width: 360px;
  }
}
